.card-nota {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: var(--fundo-detalhes);
    border-radius: 4px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
    padding: 16px;
  }
  .card-nota_cabecalho {
    font-size: 1.1em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .card-nota_texto {
    margin-top: 8px;
    font-size: 1em;
  }
  
  .card-nota_titulo {
    font-size: 1.2em;
    margin-right: 12px;
  }
  

.lista-notas {
    background-color: var(--fundo);
    -webkit-flex-grow: 3;
            flex-grow: 3;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    list-style-type: none;
    padding-left: 24px;
    padding-top: 24px;
  }
  .lista-notas_item {
    margin: 12px;
    max-width: 240px;
    max-height: 400px;
    display: inline;
  }
  
.form-cadastro {
    -webkit-flex-grow: 2;
            flex-grow: 2;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
    z-index: 1;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 12px 40px;
  }
  .form-cadastro_input {
    margin-top: 8px;
    padding: 4px;
    border: none;
    background-color: var(--fundo-detalhes);
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 100;
  }
  
  .form-cadastro_submit {
    -webkit-align-self: flex-end;
            align-self: flex-end;
    width: 30%;
    background-color: var(--secundaria);
    color: white;
    font-weight: 400;
  }
  
  .form-cadastro_submit:active {
    background-color: var(--secundaria-ativa);
  }
.conteudo {
    display: -webkit-flex;
    display: flex;
  }
  
  :root {
    --primaria: #62A5C8;
    --secundaria: #62A5C8;
    --secundaria-ativa: #DBF3E1;
    --fundo: #F7C9B6;
    --fundo-detalhes: #F3F6FE;
  }
  
body,
ul, 
div {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* accessibility */
:focus {
  outline: 0;
}


html {
  font-size: 100%; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
  -ms-text-size-adjust: 100%; /* 2 */
}

input,
select,
textarea {
  font-size: 100%; /* 1 */
  margin: 0; /* 2 */
  vertical-align: baseline; /* 3 */
  vertical-align: middle; /* 3 */
}


button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button; /* 2 */
  cursor: pointer; /* 3 */
  *overflow: visible; /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
  cursor: default;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::selection {
  background: #FFAEBC;
  text-shadow: none;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #FFAEBC;
  color: #000;
  padding: 0.2em 0;
}

.form-cadastro{
  background-color: #F5E4D8;
}
